@import "sass/helpers/variables";
header {
  .navbar {
    .nav-item {
      &.dropdown {
        > a:hover {
          cursor: text;
        }
        &.last > a:hover {
          cursor: pointer;
        }
      }
      li {
        .badge {
          text-transform: uppercase;
          font-size: 8px;
          font-weight: 400;
          padding: 4px 6px;
          border-radius: 3px;
          line-height: normal;
          margin-left: 5px;
          &-secondary {
            background-color: #b3b3b3;
          }
          &-danger {
            background-color: $primaryColor;
          }
        }
      }
      @media (min-width: 992px) {
        &.dropdown {
          position: relative;
    
          &:hover {
            .dropdown--menu {
              opacity: 1;
              visibility: visible;
              transform: translateY(0);
            }
    
            .dropdown-toggle {
              &::after {
                transform: rotate(180deg);
              }
            }
          }
    
          .dropdown--menu {
            position: absolute;
            border-radius: 5px;
            opacity: 0;
            visibility: hidden;
            transition: opacity, transform 1s cubic-bezier(0.4, 0, 0.2, 1);
            background: #FFFFFF;
            box-shadow: 0px 8px 20px rgba(0, 0, 0, 0.3);
            transform: translateY(10px);
            z-index: 9999;
            display: flex;
            left: -158px;
            width: 900px;

            ul:not(:last-child) {
              &::after {
                display: none;
              }
            }
            /* Styling for second triangle (border) */
    
            &:before {
              border-left: 12px solid transparent;
              border-right: 12px solid transparent;
              border-bottom: 12px solid #fff;
              top: -10px;
              left: 175px;
              content: "";
              position: absolute;
            }
            
            .vpn-apps {
              width: 77%;
              border-right: 1px solid #E8E8E8;
              margin: 20px 0;
              padding-right: 20px;
              ul {
                float: left;
                &.first {
                  width: 28%;
                }
                &:not(:first-of-type) {
                  width: 25%
                }
                &:nth-child(4) {
                  width: 22%;
                }
              }
            }
            .smartdns-apps {
              margin: 20px 0;
              width: 23%;
              padding-right: 20px;
            }
    
            ul {
              position: relative;
              padding: 0;
              li {
                margin-bottom: 14px;
                font-size: 12px;
                &.active {
                  a {
                    color: $primaryColor !important;
    
                    svg {
                      path {
                        fill: $primaryColor;
    
                        &.line {
                          stroke: $primaryColor;
                          fill: none;
                        }
                      }
                    }
                  }
                }
    
                &:last-child {
                  margin-bottom: 0;
                }
    
                &.disabled {
                  a:hover {
                    color: #5a5761;
                    cursor: text;
                    svg {
                      path {
                        fill: #5a5761;
                        &.line {
                          stroke: #5a5761;
                        }
                      }
                    }
                  }
                }
    
                a {
                  display: block;
                  padding: 5px 0;
                  transition: color 0.2s ease-in;
                  color: #5a5761;
                  font-family: "Averta";
                  font-weight: 500;
                  font-size: 14px !important;
    
                  &:hover {
                    color: $primaryColor;
    
                    svg {
                      path {
                        fill: $primaryColor;
    
                        &.line {
                          stroke: #fd3e72;
                          fill: none;
                        }
                      }
                    }
                  }
    
                  svg {
                    position: relative;
                    top: -3px;
                    margin-right: 5px;
    
                    path {
                      transition: all 0.2s ease-in;
                    }
                  }
                }
    
                
              }
            }
    
            &.first {
              h6 {
                padding: 0 20px;
                @media #{$mobile} {
                  margin-bottom: 20px;
                }
              }
              ul {
                padding: 10px 0 0 20px;
                li {
                  margin-bottom: 5px;
                  font-size: 14px;
                  &:first-child,
                  &.browser {
                    color: #8d8b92;
                    border-bottom: 1px solid #F2F2F2;
                    padding-bottom: 5px;
                    margin-bottom: 10px
                  }
                }
              }
            }
    
            &.second {
              width: 785px;
              left: 0;
              display: flex;
              &::before {
                left: 55px;
              }
              > div {
                width: 33.33%;
                margin: 20px 0;
                padding: 0 20px;
                h6 {
                  border-bottom: 1px solid #E8E8E8;
                  padding-bottom: 5px;
                }
              }
              .benefits-col,.features-col {
                border-right: 1px solid #E8E8E8;
              }
              ul {
                padding-top: 15px;
                li {
                  font-size: 14px;
                  font-weight: 500;
                  &:not(:last-child) {
                    margin-bottom: 5px;
                  }
                }
              }
            }
            
            &.third {
              width: 160px;
              left: 0;
    
              &:before {
                left: 24px;
              }
              &:after {
                display: none;
              }
    
              ul {
                padding: 20px;
                width: 100%;
                li {
                  font-size: 14px;
                  font-weight: 500;
                  &:not(:last-child) {
                    margin-bottom: 5px;
                  }
                }
              }
    
            }
          }
        }
      }
    
      @media #{$maxTab} {
        &.dropdown {
          &.active {
            .dropdown--menu {
              display: block !important;
            }
    
            .dropdown-toggle {
              &:after {
                transform: rotate(180deg);
              }
            }
          }
    
          .dropdown--menu {
            padding: 0 0 0 20px;
            position: relative;
            top: -10px;
            display: none;
            justify-content: space-between;
            transition: all 0.3s ease-in-out;
            border-left: 1px solid #dfddd6;
            margin-top: 20px;
    
            h6 {
              font-size: 16px;
            }
    
            ul {
              text-align: left;
              padding-right: 0;
              position: relative;
              flex-basis: 30%;
              
              &:last-child {
                li:last-child {
                  margin-bottom: 0;
                  a {
                    margin-bottom: -12px;
                  }
                }
              }
    
              &:nth-child(2) {
                margin-right: 4%;
              }
    
              li {
                line-height: 20px;
                margin-bottom: 8px;
                &.active {
                  a {
                    color: $primaryColor !important;
    
                    svg {
                      path {
                        fill: $primaryColor;
    
                        &.line {
                          stroke: $primaryColor;
                          fill: none;
                        }
                      }
                    }
                  }
                }
    
                &:last-child {
                  margin-bottom: 20px;
                }
    
                a {
                  font-size: 14px;
                  padding: 12px 0;
                  display: inline-block;
                  transition: color 0.2s ease-in;
                  color: #5a5761;
                  opacity: 0.8;
    
                  &:hover {
                    color: $primaryColor;
    
                    svg {
                      path {
                        fill: $primaryColor;
    
                        &.line {
                          stroke: #fd3e72;
                          fill: none;
                        }
                      }
                    }
                  }
    
                  svg {
                    position: relative;
                    margin-right: 5px;
                    top: -2px;
                    path {
                      transition: all 0.2s ease-in;
                    }
                  }
                }
    
                &:first-child,
                &.browser {
                  padding: 0 0 10px;
                  margin-bottom: 0;
                }
              }
            }
    
            &.second,
            &.third {
              display: none;
              ul {
                li {
                  padding: 0;
    
                  a {
                    margin-left: 0;
                    font-weight: 500;
                    opacity: 0.8;
                  }
    
                  &:first-child {
                    a {
                      margin-top: -12px;
    
                      &:hover {
                        color: $primaryColor;
                      }
                    }
                  }
                  &:last-child {
                    margin-bottom: 0;
                    a {
                      margin-bottom: -12px;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}